import { useEffect, useState } from 'react';

// material-ui
import { Grid } from '@mui/material';

// project imports
// import EarningCard from './EarningCard';
// import PopularCard from './PopularCard';
import TotalOrderLineChartCard from './TotalOrderLineChartCard';
import TotalIncomeDarkCard from './TotalIncomeDarkCard';
import TotalIncomeLightCard from './TotalIncomeLightCard';
// import TotalGrowthBarChart from './TotalGrowthBarChart';
import { gridSpacing } from 'store/constant';

// ==============================|| DEFAULT DASHBOARD ||============================== //

const Dashboard = () => {
    const [isLoading, setLoading] = useState(true);
    const [today, setToday] = useState([]);
    const Update = () => {
        (async () => {
            const url = `http://127.0.0.1:5323/api/status`;
            const response = await fetch(url, {
                headers: { 'Content-Type': 'application/json' }
                // credentials: "include",
            });
            const result = await response.json();
            if (response.status === 200) {
                setToday(result.today);
            }
        })();
    };
    useEffect(() => {
        setLoading(false);
        Update();
    }, []);
    setTimeout(() => {
        Update();
    }, 1000);
    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12}>
                <Grid container spacing={gridSpacing}>
                    <Grid item lg={12} md={12} sm={12} xs={12}>
                        <Grid container spacing={gridSpacing}>
                            <Grid item sm={12} xs={12} md={3} lg={3}>
                                <TotalIncomeDarkCard isLoading={isLoading} text="Dispositivos conectados hoy" amount={today.Connected} />
                            </Grid>
                            <Grid item sm={12} xs={12} md={3} lg={3}>
                                <TotalIncomeDarkCard isLoading={isLoading} text="Gb copiados hoy" amount={today.CopiedGB} />
                            </Grid>
                            <Grid item sm={12} xs={12} md={3} lg={3}>
                                <TotalIncomeDarkCard isLoading={isLoading} text="Mb copiados hoy" amount={today.CopiedMB} />
                            </Grid>
                            <Grid item sm={12} xs={12} md={3} lg={3}>
                                <TotalIncomeLightCard isLoading={isLoading} text="Dinero recaudado" amount={today.CopiedGB * 5} />
                            </Grid>
                        </Grid>
                    </Grid>

                    {/* {data.map((option) => (
                        <Grid item lg={4} md={6} sm={6} xs={12}>
                            <EarningCard isLoading={isLoading} data={option} />
                        </Grid>
                    ))} */}
                </Grid>
            </Grid>
            <Grid item xs={12}>
                <Grid container spacing={gridSpacing}>
                    {/* <Grid item xs={12} md={8}>
                        <TotalGrowthBarChart isLoading={isLoading} />
                    </Grid> */}
                    <Grid item lg={12} md={12} sm={12} xs={12}>
                        <TotalOrderLineChartCard isLoading={isLoading} />
                    </Grid>
                    {/* <Grid item xs={12} md={4}>
                        <PopularCard isLoading={isLoading} />
                    </Grid> */}
                </Grid>
            </Grid>
        </Grid>
    );
};

export default Dashboard;
